import styled from "styled-components";
import { FlexCol, FlexRow } from "../../../../../components/globals/index";

import { STHeader } from "../../style";

let ew_border_red = "#E97F74";
let ew_bin_active = "#F4B7B0";

export const StylePresetsContainer = styled(FlexCol)`
	width: 100%;
`;

export const ProgressBarContainer = styled(FlexRow)`
	width: 95%;
	margin: 0 auto;
	align-items: center;
`;

export const ProgressBarBinsContainer = styled.div`
	width: 100%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
`;

export const ProgressBarBin = styled.div`
	width: 10px;
	height: 24px;
	border: 1px solid ${ew_border_red};
	border-radius: 4px;
	margin: 2px 2px;
`;

export const ProgressBarBinActive = styled(ProgressBarBin)`
	background-color: ${ew_bin_active};
`;

export const SPSTHeader = styled(STHeader)`
	white-space: nowrap;
`;



