import styled, { css } from "styled-components";
import {
    FlexRow,
} from '../../../../components/globals/index';
import { STColors,STH1, STH2, STHeader } from "../style";

export const PrefFlexContainer = styled.div`
    border-top: 1px solid salmon; 
    width: 100%;
    background-color: ${STColors.prefBG};
    padding 30px 5%;
`
export const StudioTitle = styled(STHeader)`
    opacity: 0.5;
    margin: 30px 0;
`;

export const PrefWrapper = styled(FlexRow)`
    height : ${props => props.height};
    // border: 1px solid gray;
    justify-content: space-between;
`
export const PrefUnitWrapper = styled.div`
    // border: 1px solid lightGrey; 
    h2 {
        text-align: left;
    }
    width: ${props => props.width};
    height : 100%;
`
export const HR = styled.hr`
    display: block;
    height: ${props => props.height || "1px"};
    border: 0;
    border-top: ${props => props.thickness || "1px"}; solid ${props => props.color || STColors.prefBG};
    margin: 1em auto;
    padding: 0; 
`

export const BioInfoType = styled(STH2)`
    width: max-content;
    height: max-content;
`;

export const BioInfoDisplay = styled(STH1)`
    display: block;
    width: ${props => props.width};
    border: 1px solid black;
    height: max-content;
    text-align: left;
    padding: 0 10px;
`

export const BioInfoUnit = styled(FlexRow)`
    height: max-content;
    align-items: center;
`


