import styled, { css } from "styled-components";
import React from "react";

import {STColors} from '../../style';
import {EWColors, FlexCol, FlexRow} from '../../../../../components/globals/index';

export const SizeTrackerContainer = styled(FlexCol)`
    width: 100%;
    height: calc(100% - 49px);
    border: 1px solid lightGrey;
    border-radius: 25px;
    justify-content: space-evenly;
`;

export const SizeTrackerUnit = styled(FlexRow)`
    width: 100%;
    justify-content: center;
`;

export const SizeType = styled.div`
    width: 30%;
    h2 {
        text-align: right;
    }
`;
export const SizeDisplayer = styled.div`
    display: inline-block;
    width: 60px;
    height: 60px;
    border: 1px solid lightGrey;
    font-family: digital-dream-narrow;
    margin: 0 20px;
    font-size: 32px;
    text-align: center;
    line-height: 60px;
`;

export const ArrowWrapper = styled(FlexCol)`
    width: 20px;
    height: 60px;
    border: 1px solid lightGrey;
    border-radius: 12px;
    background-color: #d8d8d8;
    font-size: 32px;
    line-height: 10px;
    justify-content: space-around;
    align-items: center;
    div{
        width: 10px;
        height: 10px;
        transform: rotate(45deg);
    }

    >:nth-child(1) {
        border-left: 1px solid black;
        border-top: 1px solid black;
    }

    >:nth-child(2) {
        border-right: 1px solid black;
        border-bottom: 1px solid black;
    }
`;


