import styled from "styled-components";
import {css} from "styled-components";

export const Title = styled.div`
	font-size: 22px;
	color: #e75a50;
	display: block;
	width: 100%;
	text-align: center;
`;
export const StyleBalancerWrapper = styled.div`
	height: 100%;
	width: 100%;
`;

export const StyleBalancerSliders = styled.div`
	display: flex;
	flex-direction: row;
	justify-content: center;
	height: 80%;
	border: 1px solid #d0c0c0;
    border-radius: 15px;
    margin: 0 10%;
`
export const VerticalSliderWrapper = styled.div`
	width: 26%;
	height: 100%;
	display: flex;
    flex-direction: column;
	align-items: center;
`
export const SliderTop = styled.div`
	font-size: 16px;
	margin-bottom: -15px;
`

export const SliderBottom = styled.div`
	font-size: 16px;
	transform: translateY(180PX);
`

export const Slider = styled.input`
align-self: center;
transform: rotate(90deg) translateX(100px);;
border: 0;
padding: 0;
width: 180PX;
opacity: 1;
cursor: pointer;
-webkit-appearance: none;
&::-webkit-slider-runnable-track {
	position: relative;
		height: 12px;
		margin-right: 0.5%;
		padding-right: 0vw;
		background-size: cover;
		z-index: 1;
		background-size: 100% 100%;
		background-image: url(${props => props.trackImg});
		background-repeat: no-repeat;
}
&:focus {
	outline: none;
}

&::-webkit-slider-thumb {
	-webkit-appearance: none;
	position: relative;
	border: none;
	background-image: url(${props => props.thumbImg});
	background-position: center center;
	transform: translateY(-25%);
	height: 180%;
    width: 10%;
	z-index: 100;
	background-size: contain;
	background-repeat: no-repeat;
	border-radius: 50%;
}
&::-moz-range-track {
	-webkit-appearance: none;
	position: relative;
	height: 12px;
	background-size: cover;
	z-index: 1;
	background-image: url(${props => props.trackImg});
	background-size: 100% 100%;
}
::-moz-range-thumb {
	-webkit-appearance: none;
	position: relative;
	margin-left: 0em;
	border: none;
	border-radius: 50% 50% 0;
	background-image: url(${props => props.thumbImage});
	width: 10%;
	height: 180%;
	transform: translateY(-25%);
	z-index: 100;
	background-size: inherit;
}
`	


