import React from 'react';
import {observer} from 'mobx-react';
import {
    SizeTrackerContainer,
    SizeDisplayer,
    ArrowWrapper,
    SizeTrackerUnit,
    SizeType
} from './style';
import {STHeader} from '../../style';
import {EWColors} from '../../../../../components/globals/index';
import {HoveringPop} from '../../../../../components/popover';

const SizeTracker = observer((props) => {
    const prefStore = props.store;
    const prevSize = {
        top: prefStore.preferences.top, 
        bottom: prefStore.preferences.bottom
    };
    const sizeRange = ["1", "2", "3", "4", "5"];
    // const sizeRange = ["6M", "1Y", "2Y", "3Y", "4Y", "5Y", "6Y", "7Y", "8Y", "10", "10", "12", "14"];
    
    const introInfo = " Welcome to your Style Preset Pad! Below, are six style categories we use to classify the clothingwe share with our members. Fill up your equalizer with the style ratios you would like represented in your Wardrobe. Careful, you do not have enough points to fill up each category. Choose wisely! "

    const onSizeChange = (type, update) => {
        const newIndex = sizeRange.indexOf("" + prevSize[type]) + update;
        if (newIndex < 0 || newIndex >= sizeRange.length) {
            return;
        }
        const newSize = sizeRange[newIndex];
        prefStore.setPreference(type, newSize);
    }
    return (
        <SizeTrackerContainer>
            <SizeTrackerUnit>
                <SizeType>
                    <STHeader type={"H2"} strong={true} >
                        {"TOP:"}
                    </STHeader>
                </SizeType>
                <SizeDisplayer>
                    {prefStore.preferences.top}
                </SizeDisplayer>
                <ArrowWrapper>
                    <div onClick={() => onSizeChange("top", 1)}></div>
                    <div onClick={() => onSizeChange("top", -1)}></div>
                </ArrowWrapper>
            </SizeTrackerUnit>
            <SizeTrackerUnit>
                <SizeType>
                    <STHeader type={"H2"} strong={true} >
                        {"BOTTOM:"}
                    </STHeader>
                </SizeType>
                <SizeDisplayer>
                    {prefStore.preferences.bottom}
                </SizeDisplayer>
                <ArrowWrapper>
                    <div onClick={() => onSizeChange("bottom", 1)}></div>
                    <div onClick={() => onSizeChange("bottom", -1)}></div>
                </ArrowWrapper>
            </SizeTrackerUnit>
        </SizeTrackerContainer>
    )
})

export default SizeTracker;


