import { observable, computed } from "mobx";
import axios from "axios";

export const preferenceStore = observable({
    displaySubId: null,
    UserName: null,
    preferences: {
        NextWardrobe: null,
        EWDough: null,
        top: 4,
        bottom: 4,
        color1: {
            r: "0",
            g: "0",
            b: "0",
            a: "0",
        },
        color2: {
            r: "0",
            g: "0",
            b: "0",
            a: "0",
        },
        color3: {
            r: "0",
            g: "0",
            b: "0",
            a: "0",
        },
        color4: {
            r: "0",
            g: "0",
            b: "0",
            a: "0",
        },
        patternPads: {
            stripes: true,
            graphics: null,
            dots: null,
            scales: null,
            fabricPatterns: null,
            heather: true,
            solids: null,
            logos: true,
            prints: null,
            plaid: null,
            animal: null,
            textSayings: null,
        },
        stylePresets: {
            Jazz: 2,
            Pop: 5,
            HipHop: 3,
            Indie: 2,
            JockJams: 2,
            Lullaby: 6,
        },
        LooseOrFitted: 3,
        FancyOrCasual: 3,
        TopsOrBottoms: 3,
    },
    initPreference(prefData, subId, subName) {
        this.displaySubId = subId;
        this.UserName = subName;
        const keys = Object.keys(this.preferences);
        keys.map(key => {
            if (this.preferences[key] instanceof Object) {
                this.preferences[key] = {...prefData[key]};
            } else {
                this.preferences[key] = prefData[key];
            }
        })
    },
    setPreference(prefType, newValue) {
        const oldValue = this.preferences[prefType];
        this.preferences[prefType] = newValue;
        // axios.post('/studio/' + this.displaySubId + '/change_user_preference', this.preferences)
        // .then(response => {
        //     console.log(response);
        //     alert("Preference Updated!");
        // })
        // .catch(error => {
        //     alert(error);
        //     this.preferences[prefType] = oldValue;
        // });
    }

});

export const PREF_TYPES = {
	USERNAME: "UserName",
	NEXTWARDROBE: "NextWardrobe",
	EWDOUGH: "EWDough",
	TOP: "top"
};



