import React, {Component} from 'react';
import styled from 'styled-components';
import {ScoreWordsWrapper, WordWrapper, SliderWrapper, SliderTrack, Score, Word} from './style';
import { renderReporter } from 'mobx-react';

const NoScore = () =>{
  return(
    <div>
    <p style ={{color: '#EB867C'}}>No Score Yet! </p>
    <p style ={{fontSize: '0.7em', color: '#EB867C', fontStyle: 'initial'}}>(click slider to save your score)</p>
    </div>
  )
}
class RatingTrack extends Component{
  constructor(props){
    super(props);
    this.state = {
      score : 3,
      ScoreWords : this.props.ScoreWords || [1,2,3,4,5],
      WordWidth : 100.0/this.props.ScoreWords.length
    }
  }

 
  onChangeHandler (newscore){
      this.setState({score: newscore})
  }
    render(){
      return(
      <SliderWrapper width={this.props.width || 100}>
        <ScoreWordsWrapper>
          {this.state.ScoreWords.map((word)=>{
            return(
              <WordWrapper width={this.state.WordWidth}>
                <Word>{word}</Word>
                <svg height={25} width={5}>
                  <line x1={0} y1={0} x2={0} y2={10} style={{stroke: 'rgb(142, 141, 138)', strokeWidth : 4}} />
                </svg>
              </WordWrapper>
            )
          })}
        </ScoreWordsWrapper>
        <SliderTrack
          type="range"
          min={this.props.min || 1}
          max={this.props.max || 5}
          step={this.props.step || 1}
          autofocus ={true}
          value = {this.state.score}
          trackImg = {this.props.trackImg}
          thumbImg = {this.props.thumbImg}
          style={{fontSize:"0.8em"}}
          onChange = {(event)=>this.onChangeHandler(event.target.value)}
        />
        <Score>
          {this.state.ScoreWords[this.state.score - 1]}
        </Score>
      </SliderWrapper>
    )
  }
};
export default RatingTrack;



