import React from "react";
import { FlexCol, FlexRow } from "../../../../../../components/globals/index";
import { STHeader } from "../../../style";
import {
	GenreBarsContainer,
	GenreBarBinsContainer,
	GenreBarBin,
	GenreBar,
	GenreBarButtonsContainer,
	GenreBarButtonMinus,
	GenreBarButtonPlus,
	GenreBarBinActive
} from "./genreBarStyle";

const MaxValue = 8;

const genreBar = props => {
	let genreBars = [];

	for (let genre in props.storeStylePresets) {
		let bins = [];
		let genreValue = props.storeStylePresets[genre];
		// console.log(genre, genreValue);
		for (let i = 0; i < genreValue; i++) {
			bins.unshift(<GenreBarBinActive key={`${genre}${i}`} />);
		}
		for (let i = genreValue; i < MaxValue; i++) {
			bins.unshift(<GenreBarBin key={`${genre}${i}`} />);
		}
		let genreBar = (
			<GenreBar key={genre}>
				<GenreBarBinsContainer>{bins}</GenreBarBinsContainer>
				<GenreBarButtonsContainer>
					<GenreBarButtonMinus
						onClick={() => props.minusHandler(genre, genreValue)}
					>
						-
					</GenreBarButtonMinus>
					<GenreBarButtonPlus
						onClick={() => props.plusHandler(genre, genreValue)}
					>
						+
					</GenreBarButtonPlus>
				</GenreBarButtonsContainer>
				<STHeader type={"H4"} strong>
					{genre}
				</STHeader>
			</GenreBar>
		);
		genreBars.push(genreBar);
	}

	return <GenreBarsContainer>{genreBars}</GenreBarsContainer>;
};

export default genreBar;



