import React from 'react';
import {
  ImageWrapper,
  ImagePlaceholder,
  Image
} from './style';

export const ImgWithPlaceholder = (props) => {
  const {placeholderWidth, placeholderHeight} = props;
  return (
    <ImageWrapper>
      <ImagePlaceholder
        placeholderWidth={placeholderWidth}
        placeholderHeight={placeholderHeight}
      />
      <Image
        {...props}
      />
    </ImageWrapper>
  )
}



