import styled, {css} from "styled-components";
import React from "react";

import {
	AspectRatioContainer,
	EWColors,
	FlexRow,
	P,
	H1,
	H2,
	H3,
	H4,
	H5,
	FlexCol,
	EWFF,
} from "../../../components/globals";

import {RoundButton} from '../components/newButton/RoundButton';

export const STColors = new function() {
	this.logo = '#EB867C';
	this.sectionTitles = '#ED8F85';
	this.btnHover = '#F4B7B0';
	this.highlight = '#7DDF6A';
	this.primaryText = '#908D8D';
  }();

export const STFS = {
	xxl: "32px",
	xl: "24px",
	lg: "22px",
	md: "17px",
	sm: "16px",
	xs: "15px",
	xxs: "13px"
};

export const STH1 = styled(H1)`
	font-size: ${STFS.xxl};
	font-weight: ${props => (props.strong ? "800" : "400")};
	color: ${props => props.color || "#908d8d"};
	@media (max-width: 1366px){
			font-size: 28px;
	};
	@media (max-width: 996px){
			font-size: 22px;
	};
`;

export const STH2 = styled(H2)`
	font-size: ${STFS.xl};
	font-weight: ${props => (props.strong ? "800" : "400")};
	color: ${props => props.color || "#908d8d"};
`;

export const STH3 = styled(H3)`
	font-size: ${STFS.lg};
	font-weight: ${props => (props.strong ? "800" : "300")};
	color: ${props => props.color || "#908d8d"};
`;

export const STH4 = styled(H4)`
	font-size: ${STFS.md};
	font-weight: ${props => (props.strong ? "800" : "400")};
	color: ${props => props.color || "#908d8d"};
`;

export const STH6 = styled(H4)`
	font-size: ${STFS.xs};
	font-weight: ${props => (props.strong ? "800" : "400")};
	color: ${props => props.color || "#908d8d"};
`;

export const STHeader = props => {
	switch (props.type) {
		case "H1":
			return <STH1 {...props} />;
		case "H2":
			return <STH2 {...props} />;
		case "H3":
			return <STH3 {...props} />;
		case "H4":
			return <STH4 {...props} />;
		case "H6":
		return <STH6 {...props} />;
		default:
			return <STH1 {...props} />;
	}
};
// Feedback
export const FeedbackContainer = styled.div`
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	padding: 30px 4%;
	@media (max-width: 1366px){
		padding: 30px 2%;
	};
`;
export const ProductListContainer = styled.div`
  position: relative;
  display: inline-block;
  float: left;
  width: 38%;
  height: 100%;
  overflow: scroll;
	@media (max-width: 1366px){
			width: 36%;
	};
`;

export const ProductDisplayContainer = styled.div`
	display: inline-block;
	width: 60%;
	height: 100%;
	/* margin-left: 3%; */
`;

export const ProductionDetailWrapper = styled(FlexCol)`
	position: relative;
	text-align: left;
	padding-left: 28em;
	height: 20em;
	justify-content: space-between;
	padding-bottom: 12em;
  box-sizing: content-box;
	@media (max-width: 1366px){
			padding-left: 24em;
	};
	@media (max-width: 996px){
				padding-left: 22em;
	};
	@media (max-width: 800px){
				padding-left: 18em;
	};
`;

export const ImgDisplayer = styled.div`
	position: absolute;
	left: 2%;
	top: 0;
	width: 26em;
	height: 32em;
	@media (max-width: 1366px){
		width: 22em;
	};
	@media (max-width: 996px){
		width: 20em;
	};
	@media (max-width: 800px){
		width: 16em;
	};
`;

export const SideImgList = styled(FlexCol)`
	justify-content: space-between;
	float: left;
	width: 14%;

	div {
		margin-bottom: 5px;
	}
`;

export const MainImg = styled.div`
	width: 80%;
	float: right;
`;

export const TextDisplayer = styled(FlexCol)`
	width: 100%;
	height: 65%;
`;

export const KeepItemBtn = styled(RoundButton)`
	border-color: ${STColors.logo};
	color: ${STColors.logo};
	width: max-content;
	padding: 10px 40px;
	cursor: pointer;
	h4 {
		margin: auto;
	}
`;

// Production List
export const ItemCardContainer = styled.div`
	border-bottom: 2px ${EWColors.naturalGray} solid;
	padding: 5px 0;
	width: 90%;
	margin: auto;
`;

export const ItemCardContentWrapper = styled.div`
	padding-left: 7em;
	display: flex;
	flex-direction: row;
	position: relative;
	align-items: center;
	width: 104%;
	margin-left: -2%;
	cursor: pointer;
	&:hover {
		background-color: #d8d8d8;
	}
	background-color: ${props => (props.selected ? "#d8d8d8" : null)};
	background-color: ${props => (props.inCart ? EWColors.lightgreen : null)};
	@media (max-width: 1000px){
		padding-left: 5em;
	}
`;

export const ItemCardDscrptContainer = styled(FlexRow)`
	width: 75%;
`;
export const ItemCardEventWrapper = styled.div`
	width: 25%;
`
export const ItemCardDscrptWrapper = styled.div`
	padding: 25px 0;
`;

export const CardImgFrame = styled.div`
	position: absolute;
	top: 0.5em;
	left: 4%;
	width: 4.5em;
	@media (max-width: 1000px){
		left: 2%;
	}
`;

export const CardText = styled(P)`
	color: ${EWColors.naturalGray};
	font-weight: ${props => (props.strong ? "800" : "300")};
	text-align: left;
	font-style: ${props => (props.italic ? "italic" : "unset")};
`;

export const TabsWrapper = styled.div`
	width: 100%;
	color: red;
	padding: 0 10px;
`;

const selectedStyle = css`
	border: 1px solid ${STColors.primaryText};
	border-top: none;
	color: ${STColors.sectionTitles};
	font-weight: 800;
`;

export const StyledTab = styled.button`
	width: ${props => (100 / props.width_percent).toFixed(2)}%;
	font-family: ${EWFF.text};
	outline: none;
	border: 0px;
	color: ${STColors.primaryText};
	border-top: 1px solid ${STColors.primaryText};
	font-size: 22px;
	cursor: pointer;
	${props => props.selected? selectedStyle: null}
`;

export const TabsList = styled.div`
	width: 100%;
`;

export const MonthArrowWrapper = styled(FlexRow)`
//   position: absolute;
//   bottom: 0;
  width: 100%;
  justify-content: space-around;
  margin-top: 10px;
`;

export const MonthArrow = styled.p`
  font-size: ${STFS.xxs};
  display: inline-block;
  width: max-content;
  color: ${STColors.logo};
  cursor: pointer;
  border-bottom: 1px ${STColors.logo} solid;
  visibility: ${props => props.isHidden? "hidden": "unset"};
`;

export const NextPrvBtnWrapper = styled(FlexCol)`
  position: absolute;
  bottom: 15px;
  ${props => props.right? "right": "left"}: 15px;
  width: min-content;
  justify-content: space-between;
  align-items: center;
  text-align: center;
`;
export const RatingCommnetWrapper = styled.div`
	display: flex;
	flex-direction: column;
	margin: auto;
	width: 84%;
	justify-content: center;
	align-items: center;
`

export const CommentWrapper = styled.div`
	height: 300px;
	margin: 2%;
	display: flex;
	flex-direction: column;
`;

export const CommentLabel = styled.h2`
	margin-top: 4%;
	margin-bottom: 2%;
	color: #e75a50;
	text-align: left;
`;

export const CommentContent = styled.textarea`
	margin: 0px;
	width: 100%;
	height: 200px;
	background-color: #e6e6e6;
	outline: none;
	color: #908d8d;
	font-size: 20px;

	&::placeholder {
		color: #908d8d;
		font-size: 20px;
		padding-top: 50px;
		text-align: center;
		font-style: italic;
	}

	&:focus {
		&::placeholder {
			color: transparent;
		}
	}
`;
export const OptionTitle = styled.div`
	color: rgb(235, 134, 124);
	font-size: 20px;
	align-self: flex-start;
	margin: 1.5em 0 1.5em 0;
`



