import React, { Component } from "react";
import { observer } from "mobx-react";
import swiperStore from "./store";
import "./swiper.css";
import {
  SwiperWrapper,
  SwiperBackgroundImg,
  GoBackBtn,
  FilterBtn,
  SwiperFavoriteList,
  SwiperFavoriteTable,
  SwiperFavoriteImage,
  SwiperFavoriteImageInfoth,
  SwiperFavoriteImageInfo,
  SwiperFavoriteImageInfoLine,
  SwiperFavoriteDeleteBtn
} from "./style.js";
import SA_Frame from "./static/SA_Frame.png";
import SA_Go_Back_Button from "./static/SA_Go_Back_Button.png";
import SA_Go_Back_Button_Red from "./static/SA_Go_Back_Button_Red.png";
import SA_Gray_Wheel from "./static/SA_Gray_Wheel.png";
import SA_Red_Wheel from "./static/SA_Red_Wheel.png";
import SA_X from "./static/SA_X.png";
import {H2, H3, H4, H5} from "../../../components/globals/index";

const Favorite = observer(props => {
  const favoriteClothes = props.clothes;

  const content = (
    <tbody>
      {favoriteClothes.map((n, i) => {
        return (
          <tr key={n.id}>
            <th>
              <div style={{ width: "2em", height: "3.2em" }}>
                <SwiperFavoriteImage
                  src={n.image[0]}
                  onClick={() => swiperStore.onDetailClick(n)}
                />
              </div>
            </th>
            <SwiperFavoriteImageInfoth
              onClick={() => swiperStore.onDetailClick(n)}
              style={{
                backgroundColor: i % 2 !== 0 ? "rgb(241, 240, 240)" : "none"
              }}
            >
              <SwiperFavoriteImageInfo><H5 strong={true}>{n.intro}</H5></SwiperFavoriteImageInfo>
              {/* <SwiperFavoriteImageInfoLine /> */}
            </SwiperFavoriteImageInfoth>
            <th
              style={{
                backgroundColor: i % 2 !== 0 ? "rgb(241, 240, 240)" : "none"
              }}
            >
              <SwiperFavoriteDeleteBtn
                src={SA_X}
                onClick={() => swiperStore.onDeleteClick(n.id)}
              />
            </th>
          </tr>
        );
      })}
    </tbody>
  );

  return <SwiperFavoriteTable>{content}</SwiperFavoriteTable>;
});

class SwiperFavorite extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      leaveHover: false,
      filterHover: false
    };
    this.mouseEnterLeaveBtn = this.mouseEnterLeaveBtn.bind(this);
    this.mouseEnterFilterBtn = this.mouseEnterFilterBtn.bind(this);
    this.mouseLeave = this.mouseLeave.bind(this);
  }

  loadingStop = () => {
    this.setState({
      loading: false
    });
  };

  mouseEnterLeaveBtn() {
    this.setState({
      leaveHover: true
    });
  }

  mouseEnterFilterBtn() {
    this.setState({
      filterHover: true
    });
  }

  mouseLeave() {
    this.setState({
      leaveHover: false,
      filterHover: false
    });
  }

  render() {
    return (
      <SwiperWrapper>
        <SwiperBackgroundImg src={SA_Frame} />
        <GoBackBtn
          onMouseOver={this.mouseEnterLeaveBtn}
          onMouseLeave={this.mouseLeave}
          src={
            this.state.leaveHover ? SA_Go_Back_Button_Red : SA_Go_Back_Button
          }
          onClick={() => swiperStore.onLeaveClick()}
        />
        <FilterBtn
          onMouseOver={this.mouseEnterFilterBtn}
          onMouseLeave={this.mouseLeave}
          src={this.state.filterHover ? SA_Red_Wheel : SA_Gray_Wheel}
          onClick={() => swiperStore.onFilterClick()}
        />
        <SwiperFavoriteList>
          <Favorite clothes={swiperStore.favoriteClothes} />
        </SwiperFavoriteList>
      </SwiperWrapper>
    );
  }
}

export default observer(SwiperFavorite);



