import React, { Component } from "react";
import { observer } from "mobx-react";
import swiperStore from "./store";
import "./swiper.css";
import {
  SwiperWrapper,
  SwiperBackgroundImg,
  FavoriteBtn,
  FilterBtn,
  SwiperImage,
  SwiperIntro,
  SwiperSelectBtn,
  SwiperDislikeBtn,
  SwiperLikeBtn,
  SwiperInfoBtn
} from "./style.js";
import SA_Frame from "./static/SA_Frame.png";
import SA_Favorites_Button from "./static/SA_Favorites_Button.png";
import SA_Favorites_Button_Red from "./static/SA_Favorites_Button_Red.png";
import SA_Gray_Wheel from "./static/SA_Gray_Wheel.png";
import SA_Red_Wheel from "./static/SA_Red_Wheel.png";
import SA_Info_Button from "./static/SA_Info_Button.png";
import SA_X from "./static/SA_X.png";
import SA_X_Black from "./static/SA_X_Black.png";
import SA_Heart from "./static/SA_Heart.png";
import SA_Heart_Red from "./static/SA_Heart_Red.png";
import {H2, H3, H4, H5} from "../../../components/globals/index";

const TopLayer = observer(props => {
  // console.log(props.layer);
  // console.log(props.image);
  return (
    <div>
      <div
        className={`swiper-topLayer ${
          props.layer === 1 ? props.fade : "empty"
        }`}
      >
        <SwiperImage
          src={props.image}
          onClick={() => {
            swiperStore.onDetailClick(props.curImage);
          }}
        />
        <SwiperInfoBtn
          src={SA_Info_Button}
          onClick={() => {
            swiperStore.onDetailClick(props.curImage);
          }}
        />
      </div>
      <div
        className={`swiper-topLayer ${
          props.layer === 2 ? props.fade : "empty"
        }`}
      >
        <SwiperImage
          src={props.image}
          onClick={() => {
            swiperStore.onDetailClick(props.curImage);
          }}
        />
        <SwiperInfoBtn
          src={SA_Info_Button}
          onClick={() => {
            swiperStore.onDetailClick(props.curImage);
          }}
        />
      </div>
    </div>
  );
});

const BottomLayer = observer(props => {
  // console.log(props.layer);
  // console.log(props.image);

  return (
    <div>
      <div className="swiper-bottomLayer">
        <SwiperImage
          src={props.image}
          onClick={() => {
            swiperStore.onDetailClick(props.curImage);
          }}
        />
        <SwiperInfoBtn
          src={SA_Info_Button}
          onClick={() => {
            swiperStore.onDetailClick(props.curImage);
          }}
        />
      </div>
    </div>
  );
});

class SwiperSelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      favoriteHover: false,
      filterHover: false,
      dislikeHover: false,
      likeHover: false
    };
    this.mouseEnterFavoriteBtn = this.mouseEnterFavoriteBtn.bind(this);
    this.mouseEnterFilterBtn = this.mouseEnterFilterBtn.bind(this);
    this.mouseEnterDislikeBtn = this.mouseEnterDislikeBtn.bind(this);
    this.mouseEnterLikeBtn = this.mouseEnterLikeBtn.bind(this);
    this.mouseLeave = this.mouseLeave.bind(this);
  }

  loadingStop = () => {
    this.setState({
      loading: false
    });
  };

  mouseEnterFavoriteBtn() {
    this.setState({
      favoriteHover: true
    });
  }

  mouseEnterFilterBtn() {
    this.setState({
      filterHover: true
    });
  }

  mouseEnterDislikeBtn() {
    this.setState({
      dislikeHover: true
    });
  }

  mouseEnterLikeBtn() {
    this.setState({
      likeHover: true
    });
  }

  mouseLeave() {
    this.setState({
      favoriteHover: false,
      filterHover: false,
      dislikeHover: false,
      likeHover: false
    });
  }

  componentDidMount() {
    swiperStore.fetchClothes();
  }

  componentDidUpdate() {
    if (swiperStore.clothesData.length < 5) {
      swiperStore.fetchClothes();
    }
  }

  render() {
    if (swiperStore.clothesData.length !== 0) {
      if (swiperStore.curClothes.length === 0) {
        swiperStore.setCurClothes();
      }
      return (
        <SwiperWrapper>
          <SwiperBackgroundImg src={SA_Frame} />
          <FavoriteBtn
            onMouseOver={this.mouseEnterFavoriteBtn}
            onMouseLeave={this.mouseLeave}
            src={
              this.state.favoriteHover
                ? SA_Favorites_Button_Red
                : SA_Favorites_Button
            }
            onClick={() => swiperStore.onFavoriteClick()}
          />
          <FilterBtn
            onMouseOver={this.mouseEnterFilterBtn}
            onMouseLeave={this.mouseLeave}
            src={this.state.filterHover ? SA_Red_Wheel : SA_Gray_Wheel}
            onClick={() => swiperStore.onFilterClick()}
          />
          {swiperStore.showTop === true && (
            <TopLayer
              image={swiperStore.prevClothes[0].image[0]}
              curImage={swiperStore.curClothes[0]}
              fade={swiperStore.fade}
              layer={swiperStore.layer}
              onDetailClick={swiperStore.onDetailClick}
            />
          )}
          <BottomLayer
            image={swiperStore.curClothes[0].image[0]}
            curImage={swiperStore.curClothes[0]}
            onDetailClick={swiperStore.onDetailClick}
          />
          <SwiperIntro>
            <H4 strong = {true}>{swiperStore.curClothes[0].intro}
            <br />
            MSRP: ${swiperStore.curClothes[0].price}</H4>
          </SwiperIntro>
          <SwiperSelectBtn>
            <SwiperDislikeBtn
              onMouseOver={this.mouseEnterDislikeBtn}
              onMouseLeave={this.mouseLeave}
              src={this.state.dislikeHover ? SA_X_Black : SA_X}
              onClick={() => swiperStore.onSelectClick("dislike")}
            />
            <SwiperLikeBtn
              onMouseOver={this.mouseEnterLikeBtn}
              onMouseLeave={this.mouseLeave}
              src={this.state.likeHover ? SA_Heart_Red : SA_Heart}
              onClick={() => swiperStore.onSelectClick("like")}
            />
          </SwiperSelectBtn>
        </SwiperWrapper>
      );
    } else {
      return <div />;
    }
  }
}

export default observer(SwiperSelect);



