import React from 'react';
import styled from 'styled-components';

const NextBottonWrapper = styled.div`
  display: flex;
  flex-direction: column;
`
const ButtonName = styled.p`
  font-size: 15px;
  color: rgb(142, 141, 138);
`
const NextBotton = styled.div`
  border: ${props => props.thickness} solid rgb(142, 141, 138);
  height: ${props => props.height};
  width: ${props => props.height};
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
`;
const Arrow = styled.i`
  border: 2px solid rgb(142, 141, 138);
  border-width: 0 ${props => props.thickness} ${props => props.thickness} 0;
  display: inline-block;
  padding: ${props => props.size};

  transform: rotate(${props => props.dir==="right"?'-45deg':'135deg'});
`;

const NextOrPreviousButton = (props) => {
  return (
    <NextBotton
        height={props.circleHeight ||'50px'} thickness={props.circleThickness || '3px'}
        onClick={props.clicked}>
      <Arrow dir={props.dir || 'right'} size={props.arrowSize ||'5px'} thickness={props.arrowThickness ||'3px'} />
    </NextBotton>
  )
}

export default NextOrPreviousButton;



