import React from "react";

import {observer} from "mobx-react";
import {
    PatternIcon,
    PatternPannelWraper,
    Title,
    PatternPadTop,
    FreqWrapper,
    FreqBubble,
    Dot
} from './style';
import { STHeader } from "../../style";
import {HoveringPop} from '../../../../../components/popover';
import { STColors } from "../../style";


const introInfo = `
    Click on the Pattern Picker Button to select the clothing patterns you love!
     ( and the ones you hate grrrrr!)
`
const PatternPad = observer(props =>{
    let patterns = props.store.preferences.patternPads;

    const PatternClickHandler = (pattern)=>{
        let newPatternPads =  { ...props.store.preferences.patternPads};
        
        if(newPatternPads[pattern] == null){
            newPatternPads[pattern] = true;
        }else if(newPatternPads[pattern] ==true){
            newPatternPads[pattern] = false;
        }else{
            newPatternPads[pattern] = null;
        }


        props.store.setPreference("patternPads", newPatternPads);
        console.log(pattern);
        console.log(props.store.preferences.patternPads[pattern]);
    };

    const RenderPatternicon =() =>{
        return Object.keys(patterns).map((key) =>{
          return(
              <PatternIcon 
                onClick={()=> PatternClickHandler(key)} 
                status = {patterns[key]}
              >
                {key}
              </PatternIcon>
          );
        });
    }

    return(
        
        <div className={"patternPannel"} >
         <PatternPadTop>
            <STHeader type={"H2"} color={STColors.logo} strong>
						{"PATTERN PAD: "}
						<HoveringPop 
                        content={introInfo}
                        position={"bottom"}
                    	/>
			</STHeader>
            <FreqWrapper>
                Aways
                <FreqBubble>
                    <Dot color={"#7ddf6a"}></Dot>
                </FreqBubble>
            </FreqWrapper>
            <FreqWrapper>
                Sometimes
                <FreqBubble>
                    <Dot color={"#8e8d8a"}></Dot>
                </FreqBubble>
            </FreqWrapper>
            <FreqWrapper>
                Never
                <FreqBubble>
                    <Dot color={"#e75a50"}></Dot>
                </FreqBubble>
            </FreqWrapper>
        </PatternPadTop>
            <PatternPannelWraper>
               <RenderPatternicon />
            </PatternPannelWraper>
        </div>
    );

});

export default PatternPad; 


