import {LOGIN, LOGOUT} from '../actions/authentication';

const initialState = {
  currentUser: null,
}

export default function userReducer (state = initialState, action){
  switch (action.type) {
    case LOGIN:
      fetch('/d/users/sign_in', {
        method: "post",
        headers: {
          authenticity_token: '2/mxBI/zWUtwjBhAfEUXo68Kf8OdOMoruayfM69fcF1V3tcB55eFEJhAyQbOmjTqVLV0Y0f1CB/tqe17f1trRQ==',
          utf8: true,
        },
        data: {
          user: {
            email: "test@gmail.com",
            password: "122iwajeglk",
            remember_me: 0,
          }
        }
      })
      .then(response => {console.log(response)});
      return {
        currentUser: action.data.username,
      }
    case LOGOUT:
      return {
        currentUser: null
      }
    default:
      return state;
  }
}



