import React from "react";
import {
	StylePresetsContainer,
	ProgressBarContainer,
	ProgressBarBin,
	ProgressBarBinsContainer,
	ProgressBarBinActive,
	SPSTHeader
} from "./style";

import { STColors } from "../../style";
import GenreBar from "./genreBar/genreBar";
import { observer } from "mobx-react";
import {HoveringPop} from '../../../../../components/popover';

const introInfo = " Welcome to your Style Preset Pad! Below, are six style categories we use to classify the clothingwe share with our members. Fill up your equalizer with the style ratios you would like represented in your Wardrobe. Careful, you do not have enough points to fill up each category. Choose wisely! "
const stylePresets = observer(props => {
	const storeStylePresets = props.store.preferences.stylePresets;
	let stylePresetsMax = 36;
	let currentStyleOccupied = 0;
	for (let property in storeStylePresets) {
		currentStyleOccupied += storeStylePresets[property];
	}
	// console.log(props.store.preferences.stylePresets);
	let progressBarBins = [];

	for (let i = 0; i < currentStyleOccupied; i++) {
		progressBarBins.unshift(<ProgressBarBin key={i} />);
	}

	for (let i = 0; i < stylePresetsMax - currentStyleOccupied; i++) {
		progressBarBins.unshift(
			<ProgressBarBinActive key={currentStyleOccupied + i} />
		);
	}

	const plusHandler = (name, value) => {
		if (value < 8 && currentStyleOccupied < stylePresetsMax) {
			let newStylePresets = { ...props.store.preferences.stylePresets };
			newStylePresets[name] += 1;
			props.store.setPreference("stylePresets", newStylePresets);
		}
	};

	const minusHandler = (name, value) => {
		if (value > 0 && currentStyleOccupied <= stylePresetsMax) {
			let newStylePresets = { ...props.store.preferences.stylePresets };
			newStylePresets[name] -= 1;
			props.store.setPreference("stylePresets", newStylePresets);
		}
	};

	return (
		<div>
			<StylePresetsContainer>
				<ProgressBarContainer>
					<SPSTHeader type={"H2"} color={STColors.logo} strong>
						{"STYLE PRESETS: "}
						<HoveringPop 
                        content={introInfo}
                        position={"bottom"}
                    	/>
					</SPSTHeader>
					<ProgressBarBinsContainer>
						{progressBarBins}
					</ProgressBarBinsContainer>
				</ProgressBarContainer>
				<GenreBar
					storeStylePresets={storeStylePresets}
					plusHandler={plusHandler}
					minusHandler={minusHandler}
				/>
			</StylePresetsContainer>
		</div>
	);
});

export default stylePresets;



