import React from 'react';
import styled from 'styled-components';
import { BurgerSlice,
         BurgerWrapper, } from './style';
import {
  NavTab,
  EWColors,
  EWTextWithBackground,
  EWCickableText,
  RemoveMarginPadding,
  EWFF,
  EWFS
} from '../globals';

export const buttonType = {
  secondary: `background-color: ${EWColors.apricot}; border: none;`,

  primary: `
    border: solid ${EWColors.apricot} 1px;
    background-color: white;
  `
}


export const EWButtonBasic = styled.button`
  ${RemoveMarginPadding};
  font-family: ${EWFF.basic};
  padding: ${props => {
    switch (props.size) {
      case 'zero':
        return '0';
      case 'sm':
        return '0.5em 1em 0.5em 1em';
      case 'md':
        return '0.9em 2em 0.9em 2em';
      case 'lg':
        return '1em 3em 1em 3em';
      default:
        return '0.9em 2em 0.9em 2em';
    }
  }};

  ${props => {
    if(buttonType[props.type]){
      return buttonType[props.type];
    }else{
      return buttonType.primary;
    }
  }};

  font-size: ${props => {
    if(EWFS[props.textSize]){
      return EWFS[props.textSize];
    }else{
      return EWFS['md'];
    }
  }}
`


export const EWButton = (props) => (
  <div>
    <EWButtonBasic {...props} />
  </div>
)


export const BurgerButton = (props) => (
    <BurgerWrapper width={props.width}
                   hideAt={props.hideAt}
                   onClick={props.clicked}>
        <BurgerSlice />
        <BurgerSlice />
        <BurgerSlice />
    </BurgerWrapper>
 );

export const NavButton = styled(NavTab)`
  color: white;
  background-color: ${EWColors.burntSienna};
  transition: ease all 0.4s;
  text-align: center;
  &:hover{
    opacity: 0.7;
    color: white;
  }
`

export const BasicButton = styled.button`
  border: none;
`

export const PureTextButton = styled(BasicButton)`
  border: none;
  background: none;
  ${EWCickableText};
`

export const Button = styled(BasicButton)`
  ${EWTextWithBackground};
  transition: ease all 0.4s;
  text-align: center;
  &:hover{
    opacity: 0.7;
    color: white;
  }
  width: ${props => props.width || '30%'};
  padding: 7.5px;
  margin: 45px auto 45px auto
  @media (max-width: 600px){
    width: 100%;
  }
`



