import styled from 'styled-components';
import {EWColors} from '../../components/globals';

export const ItemsListWrapper = styled.div`
`;

export const Column = styled.span`
  width : ${props => props.width || '0px'};
  display: flex;
  justify-content: space-around;
  align-items: center;
`

export const ItemsListHeaderWrapper = styled.div`
  border: solid ${EWColors.naturalGray} 1px;
  display: flex;
  width: 100%;
  justify-content: space-around;
`;

export const ItemListRowWrapper = styled.div`
`;

export const ItemListCardWrapper = styled.div`
  border: solid ${EWColors.naturalGray} 1px;
  display: flex;
  width: 100%;
  justify-content: space-around;
  background-color: ${props => props.selected && EWColors.aquaIsland};
`;

export const ItemListCardImageFrame = styled.div`
  width: 20%;
`

export const ItemListCardImageWrapper = styled.div`
  max-width: 100px;
  margin: auto;
  min-width: 50px;
`

export const ItemListCardInfoFrame = styled.div`
  width: 60%;
`

export const ItemListCardInfoWrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-around;
`



