import React from "react";
import { observer } from "mobx-react";

import { PrefFlexContainer, PrefWrapper, PrefUnitWrapper, HR, BioInfoType, BioInfoDisplay, BioInfoUnit, StudioTitle } from "./style";
import { STHeader, STColors } from "../style";

import {HoveringPop} from '../../../../components/popover';
import {preferenceStore} from '../preferenceStore';
import swiperStore from "../../swiper/store";
import SwiperSelect from "../../swiper/swiperSelect";
import SwiperDetail from "../../swiper/swiperDetail";
import SwiperFavorite from "../../swiper/swiperFavorite";
import SwiperFilter from "../../swiper/swiperFilter";
import SweetAlert from "react-bootstrap-sweetalert";
import SA_X from "../../swiper/static/SA_X.png";
import { SwiperHeader, SwiperBtn } from "../../swiper/style";
import SizeTracker from './sizeTracker/sizeTracker';
import StylePresets from "./stylePresets/stylePresets";
import PatternPad from "./patternPad/patternPad";
import StyleBalancer from "./StyleBalance/styleBalancer";
import ColorPickerSet from "../../colorPickerSet/colorPickerSet";
const infoMap = {
    outfitPicker: "Your Outfit Picker is a \"swipping\" app that saves your style and outfit preferences. Let your little give it a try! In settings, you can specify the clothing type, style, material, and so much more. Take a look! ",
    colorPicker: "Check it out! Totally customizable color pickers! Use the sliders to create your four favourite colors. Your change will automatically save. Get ready to see and wear these shades in your next Wardrobe!",
    sizeUpdate: "Let us know if your children have grown! (or shrunk?)",
    styleBalance: "The Style Balance sliders tell the stylelists how we can best serve your family. how do you like your clothes to fit? Would you like more of one type of clothing? These are some of the questions you sliders will answer.",
    EverlastingWardrobee: "Welcome to your STUDIO, youre in control! At the helm if you will. From here, parents and children can share and update their sizes, styles, and preferences around-the-clock. No need to save your work. STUDIO saves all of your changes five seconds after youve made them. So go ahead, change your mind, and change it often! Theres a whole world of clothes out there waiting to be discovered... By You!"
};

export const Preference = observer(() => {
    console.log(preferenceStore);
    return (
    
        <PrefFlexContainer height={"100px"}>
            <StudioTitle type={"H1"} strong>
                {"STUDIO by Everlasting Wardrobe "}
                <HoveringPop 
                    content={infoMap.EverlastingWardrobee}
                    position={"bottom"}
                />
            </StudioTitle>
            <PrefWrapper height={"65px"}>
                <BioInfoUnit>
                    <BioInfoType color={STColors.logo} strong >
                        {"NAME: "}
                    </BioInfoType>
                    <BioInfoDisplay color={"black"} width={"200px"}>
                        {"Charlotte"}
                    </BioInfoDisplay>
                </BioInfoUnit>
                <BioInfoUnit>
                    <BioInfoType color={STColors.logo} strong >
                        {"NEXT WARDROBE: "}
                    </BioInfoType>
                    <BioInfoDisplay color={"black"} width={"300px"}>
                        {"September 24"}
                    </BioInfoDisplay>
                </BioInfoUnit>
                <BioInfoUnit>
                    <BioInfoType color={STColors.logo} strong >
                        {"EW DOUGH: "}
                    </BioInfoType>
                    <BioInfoDisplay color={"black"} width={"50px"}>
                        {"5"}
                    </BioInfoDisplay>
                </BioInfoUnit>
            </PrefWrapper>
            <HR width ={"80%"} color={"red"} />
            <PrefWrapper height={"300px"}>
                <PrefUnitWrapper width={"30%"}>
                    <STHeader type={"H2"} color={STColors.logo} strong >
                        {"SIZE UPDATE: "}
                        <HoveringPop 
                        content={infoMap.sizeUpdate}
                        position={"bottom"}
                        />
                    </STHeader>
                    <SizeTracker store={preferenceStore}/>
                </PrefUnitWrapper>
                <PrefUnitWrapper width={"60%"}>
                    <StylePresets store={preferenceStore} />
                </PrefUnitWrapper>
            </PrefWrapper>
            <PrefWrapper height={"400px"}>
                <PrefUnitWrapper width={"30%"}>
                    <STHeader type={"H2"} color={STColors.logo} strong >
                        {"OUTFIT PICKER: "}
                        <HoveringPop 
                        content={infoMap.outfitPicker}
                        position={"bottom"}
                        />
                    </STHeader>
                    <img style={{    
                                     width: "70%",
                                     height: "88%"}}
                     src={"https://res.cloudinary.com/the-wardrobe/image/upload/v1547072806/Swipe%20App/SA_Image.png"} 
                     onClick = { ()=> swiperStore.onSwiperClick()}/>
                     <SweetAlert
                        show={swiperStore.swiper}
                        showConfirm={false}
                        closeOnClickOutside={true}
                        onCancel={() => swiperStore.onSwiperClick()}
                        style={{ top: "28%", left: "46%", width: "38em", height: "43.5em" }}
        >
                        <SwiperHeader>
                            <SwiperBtn src={SA_X} onClick={() => swiperStore.onSwiperClick()} />
                        </SwiperHeader>
                        <div>
                            {swiperStore.swiperSelect === true && <SwiperSelect />}
                            {swiperStore.swiperDetail === true && <SwiperDetail />}
                            {swiperStore.swiperFavorite === true && <SwiperFavorite />}
                            {swiperStore.swiperFilter === true && <SwiperFilter />}
                        </div>
                    </SweetAlert>
                </PrefUnitWrapper>
                <PrefUnitWrapper width={"60%"}>
                    <STHeader type={"H2"} color={STColors.logo} strong
                        style={{paddingLeft: "2.5%"}}
                    >
                        {"COLOR PICKER: "}
                        <HoveringPop 
                        content={infoMap.colorPicker}
                        position={"bottom"}
                        />
                    </STHeader>
                   <ColorPickerSet />
                </PrefUnitWrapper>
            </PrefWrapper>
            <PrefWrapper height={"300px"}>
                <PrefUnitWrapper width={"30%"}>
                    <STHeader type={"H2"} color={STColors.logo} strong >
                        {"STYLE BALANCE "}
                        <HoveringPop 
                        content={infoMap.styleBalance}
                        position={"bottom"}
                        />
                    </STHeader>
                    <StyleBalancer store = {preferenceStore} />
                </PrefUnitWrapper>
                <PrefUnitWrapper width={"60%"} style={{textAlign: "left"}}>
                    <PatternPad store={preferenceStore} />
                </PrefUnitWrapper>
            </PrefWrapper>
        </PrefFlexContainer>
    );
})


