export const subjectsSelector = (ids, subjectsMap) => {
    return ids.map((id)=>{
      return subjectsMap[id];
    })
  }

export const extractAbstract = (content) => {
    let startIndex = content.indexOf(">");
    let counter = 0;
    let abstract = [];
    while (counter < 100 && startIndex < content.length) {
        const leftBound = content.indexOf(">", startIndex) + 1;
        const rightBound = content.indexOf("<", startIndex);
        if (rightBound === -1) break;
        const para = content.substring(leftBound, rightBound);
        abstract.push(para);
        counter += para.length;
        startIndex = rightBound + 1;
    }
    return abstract.join("");
}

