// this function should set this to be used.
export const flattenSubjectToHash = function(obj, property, key){
    const subjectMap = {};
    if(!obj[property] || !Array.isArray(obj[property]) || !obj[property][0][key]){
      return null;
    }
    const subjectList = obj[property].map(subject => {
      subjectMap[subject[key]] = subject;
      return subject[key];
    });
    if(this){
      this[property+'Map'] = subjectMap;
    }
    return subjectList;
}



