import styled from 'styled-components';
import {H4, H5} from '../../../../components/globals/index';

export const ScoreWordsWrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
`;

export const WordWrapper = styled.div`
  display: flex;
  align-items: center;
  flex-direction: column;
  width : ${props => props.width + '%'};
  font-size: 10px;
`;
export const SliderWrapper = styled.div`
  width: ${props => props.width + '%'};
`;

export const SliderTrack = styled.input`
  align-self: center;
  border: 0;
  padding: 0;
  width: 90%;
  margin-top: 1%;
  margin-bottom: 3%;
  opacity: 1;
  cursor: pointer;
  -webkit-appearance: none;
  &::-webkit-slider-runnable-track {
    position: relative;
    height: 1vw;
    margin-right: 0.5%;
    padding-right: 0vw;
    background-size: cover;
    z-index: 1;
    background-size: 100% 100%;
    background-image: url(${props => props.trackImg});
    background-repeat: no-repeat;
  };
  &:focus {
    outline: none;
  }

  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    position: relative;
    margin-top: -0.7vw;
    border: none;
    background-image: url(${props => props.thumbImg});
    background-position: center center;
    height: 250%;
    width: 10%;
    z-index: 100;
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 50%;
  };
  &::-moz-range-track{
    -webkit-appearance: none;
    position: relative;

    height: 1vw;
    background-size: cover;
    z-index: 1;
    background-image: url(${props => props.trackImg});
    background-size: 100% 100%;
  };
  ::-moz-range-thumb {
    -webkit-appearance: none;
    position: relative;
    margin-left: 0em;
    border: none;
    border-radius: 50% 50% 0;
    background-image: url(${props => props.thumbImage});
    width: 10%;
    height: 250%;
    z-index: 100;
    background-size: inherit;
  }
`;
export const Score = styled(H4)`
  color: rgb(142, 141, 138);
  font-weight: 400;
  font-style: italic;
  height: 1rem;
`
export const Word = styled(H5)`
  color: rgb(142, 141, 138);
  margin-bottom: 5%;
  font-weight: 400;
`



