import React from "react";
import {
    Slider,
    Title,
    StyleBalancerWrapper,
    StyleBalancerSliders,
    VerticalSliderWrapper,
    SliderBottom,
    SliderTop
} from "./style";

import { observer } from "mobx-react";
import RatingTrack from "../../../components/RatingTrack/RatingTrack";

const StyleBalancer = observer(props =>{
    const boxesStore = props.store;
    let storeLooseOrFitted = props.store.preferences.LooseOrFitted;
    console.log(props.store.preferences.patternPads.stripes);
    let storeFancyOrCasual = props.store.preferences.FancyOrCasual;
    let storeTopsOrBottoms = props.store.preferences.TopsOrBottoms;
    return(
        <StyleBalancerWrapper>
            {/* <Title>Style Balance</Title> */}
            <StyleBalancerSliders>
                <VerticalSliderWrapper>
                    <SliderTop>Loose</SliderTop>
                    <Slider
                        type="range"
                        orient="vertical"
                        autoFocus={true}
                        // style={{ fontSize: "0.8em"}}
                        step={1} 
                        min={1} 
                        max={5}
                        
                        trackImg={
                            "https://res.cloudinary.com/the-wardrobe/image/upload/v1541706654/Style_and_Fit_Slider.png"
                        }
                        thumbImg={
                            "https://res.cloudinary.com/the-wardrobe/image/upload/v1541706654/Style_and_Fit_Slider_Knob.png"
                        } 
                    />
                    <SliderBottom>Fitted</SliderBottom>
                </VerticalSliderWrapper>
                <VerticalSliderWrapper>
                    <SliderTop>Fancy</SliderTop>
                    <Slider
                        type="range"
                        orient="vertical"
                        autoFocus={true}
                        // style={{ fontSize: "0.8em" }}
                        step={1} 
                        min={1} 
                        max={5}
                        
                        trackImg={
                            "https://res.cloudinary.com/the-wardrobe/image/upload/v1541706654/Style_and_Fit_Slider.png"
                        }
                        thumbImg={
                            "https://res.cloudinary.com/the-wardrobe/image/upload/v1541706654/Style_and_Fit_Slider_Knob.png"
                        } 
                    />
                    <SliderBottom>Casual</SliderBottom>
                </VerticalSliderWrapper>
                <VerticalSliderWrapper>
                    <SliderTop>Tops</SliderTop>
                    <Slider
                        type="range"
                        orient="vertical"
                        autoFocus={true}
                        // style={{ fontSize: "0.8em" }}
                        step={1} 
                        min={1} 
                        max={5}
                        trackImg={
                            "https://res.cloudinary.com/the-wardrobe/image/upload/v1541706654/Style_and_Fit_Slider.png"
                        }
                        thumbImg={
                            "https://res.cloudinary.com/the-wardrobe/image/upload/v1541706654/Style_and_Fit_Slider_Knob.png"
                        } 
                    />
                    <SliderBottom>Bottoms</SliderBottom>
                </VerticalSliderWrapper>
            </StyleBalancerSliders>
        </StyleBalancerWrapper>
    );
});

export default StyleBalancer;



