import React, { Component } from "react";
import { observer } from "mobx-react";
import swiperStore from "./store";
import "./swiper.css";
import {
  SwiperWrapper,
  SwiperBackgroundImg,
  GoBackBtn,
  FilterBtn,
  SwiperFilterWrapper,
  SwiperFilterTitle,
  SwiperFilterTitleTable,
  SwiperFilterTableWrapper,
  SwiperFilterTable,
  SwiperFilterOption,
  SwiperFiltereBtn,
  SwiperAnnotation,
  SwiperEW
} from "./style.js";
import SA_Frame from "./static/SA_Frame.png";
import SA_Go_Back_Button from "./static/SA_Go_Back_Button.png";
import SA_Go_Back_Button_Red from "./static/SA_Go_Back_Button_Red.png";
import SA_Red_Wheel from "./static/SA_Red_Wheel.png";
import SA_Radio_Button from "./static/SA_Radio_Button.png";
import SA_Radio_Button_Both from "./static/SA_Radio_Button_Both.png";
import SA_EW from "./static/SA_EW.png";
import {H2, H3, H4, H5} from "../../../components/globals/index";

const ClothingGender = observer(props => {
  const store = props.store;
  return (
    <SwiperFilterTableWrapper>
      <SwiperFilterTable>
        <tbody>
          <tr>
            <td style={{ width: "33%" }}>
              <SwiperFilterOption>
                <SwiperFiltereBtn
                  src={
                    store.filterGenderBoys === true
                      ? SA_Radio_Button_Both
                      : SA_Radio_Button
                  }
                  onClick={() => store.onFilterGenderBoysClick()}
                />
                <H5 strong = {true}>Boys</H5>
              </SwiperFilterOption>
            </td>
            <td style={{ width: "33%" }}>
              <SwiperFilterOption>
                <SwiperFiltereBtn
                  src={
                    store.filterGenderGirls === true
                      ? SA_Radio_Button_Both
                      : SA_Radio_Button
                  }
                  onClick={() => store.onFilterGenderGirlsClick()}
                />
                <H5 strong = {true}>Girls</H5>
              </SwiperFilterOption>
            </td>
            <td style={{ width: "33%" }}>
              <SwiperFilterOption>
                <SwiperFiltereBtn
                  src={
                    store.filterGenderNeutral === true
                      ? SA_Radio_Button_Both
                      : SA_Radio_Button
                  }
                  onClick={() => store.onFilterGenderNeutralClick()}
                />
                <H5 strong = {true}>Neutral</H5>
              </SwiperFilterOption>
            </td>
          </tr>
        </tbody>
      </SwiperFilterTable>
    </SwiperFilterTableWrapper>
  );
});

const ClothingStylesLineOne = observer(props => {
  const store = props.store;
  return (
    <SwiperFilterTableWrapper>
      <SwiperFilterTable>
        <tbody>
          <tr>
            <td style={{ width: "33%" }}>
              <SwiperFilterOption>
                <SwiperFiltereBtn
                  src={
                    store.filterStyleTops === true
                      ? SA_Radio_Button_Both
                      : SA_Radio_Button
                  }
                  onClick={() => store.onFilterStyleTopsOnlyClick()}
                />
                <H5 strong = {true}>Tops</H5>
              </SwiperFilterOption>
            </td>
            <td style={{ width: "33%" }}>
              <SwiperFilterOption>
                <SwiperFiltereBtn
                  src={
                    store.filterStyleBottoms === true
                      ? SA_Radio_Button_Both
                      : SA_Radio_Button
                  }
                  onClick={() => store.onFilterStyleBottomsOnlyClick()}
                />
                <H5 strong = {true}>Bottoms</H5>
              </SwiperFilterOption>
            </td>
            <td style={{ width: "33%" }}>
              <SwiperFilterOption>
                <SwiperFiltereBtn
                  src={
                    store.filterStyleDresses === true
                      ? SA_Radio_Button_Both
                      : SA_Radio_Button
                  }
                  onClick={() => store.onFilterStyleDressesClick()}
                />
                <H5 strong = {true}>Dresses</H5>
              </SwiperFilterOption>
            </td>
          </tr>
        </tbody>
      </SwiperFilterTable>
    </SwiperFilterTableWrapper>
  );
});

const ClothingStylesLineTwo = observer(props => {
  const store = props.store;
  return (
    <SwiperFilterTableWrapper>
      <SwiperFilterTable>
        <tbody>
          <tr>
            <td style={{ width: "33%" }} />
            <td style={{ width: "33%" }}>
              <SwiperFilterOption>
                <SwiperFiltereBtn
                  src={
                    store.filterStyleOnePiece === true
                      ? SA_Radio_Button_Both
                      : SA_Radio_Button
                  }
                  onClick={() => store.onFilterStyleOnePieceClick()}
                />
                <H5 strong = {true}>One-Piece</H5>
              </SwiperFilterOption>
            </td>
            <td style={{ width: "33%" }}>
              <SwiperFilterOption>
                <SwiperFiltereBtn
                  src={
                    store.filterStylePajamas === true
                      ? SA_Radio_Button_Both
                      : SA_Radio_Button
                  }
                  onClick={() => store.onFilterStylePajamasClick()}
                />
                <H5 strong = {true}>Pajamas</H5>
              </SwiperFilterOption>
            </td>
          </tr>
        </tbody>
      </SwiperFilterTable>
    </SwiperFilterTableWrapper>
  );
});

class SwiperFilter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      leaveHover: false,
      filterHover: false
    };
    this.mouseEnterLeaveBtn = this.mouseEnterLeaveBtn.bind(this);
    this.mouseEnterFilterBtn = this.mouseEnterFilterBtn.bind(this);
    this.mouseLeave = this.mouseLeave.bind(this);
  }

  loadingStop = () => {
    this.setState({
      loading: false
    });
  };

  mouseEnterLeaveBtn() {
    this.setState({
      leaveHover: true
    });
  }

  mouseEnterFilterBtn() {
    this.setState({
      filterHover: true
    });
  }

  mouseLeave() {
    this.setState({
      leaveHover: false,
      filterHover: false
    });
  }

  render() {
    return (
      <SwiperWrapper>
        <SwiperBackgroundImg src={SA_Frame} />
        <GoBackBtn
          onMouseOver={this.mouseEnterLeaveBtn}
          onMouseLeave={this.mouseLeave}
          src={
            this.state.leaveHover ? SA_Go_Back_Button_Red : SA_Go_Back_Button
          }
          onClick={() => swiperStore.onLeaveClick()}
        />
        <FilterBtn src={SA_Red_Wheel} />
        <SwiperFilterWrapper>
          <SwiperFilterTitle>
            <SwiperFilterTitleTable>
              <tbody>
                <tr>
                  <td><H4 strong = {true}>Clothing Gender</H4></td>
                </tr>
              </tbody>
            </SwiperFilterTitleTable>
          </SwiperFilterTitle>
          <ClothingGender store={swiperStore} />
          <SwiperFilterTitle>
            <SwiperFilterTitleTable><H4 strong = {true}>Clothing Styles</H4></SwiperFilterTitleTable>
          </SwiperFilterTitle>
          <ClothingStylesLineOne store={swiperStore} />
          <ClothingStylesLineTwo store={swiperStore} />
        </SwiperFilterWrapper>
        <SwiperAnnotation>
          <SwiperEW src={SA_EW} />
        </SwiperAnnotation>
      </SwiperWrapper>
    );
  }
}

export default observer(SwiperFilter);



