const swiperStore = observable({
  swiper: false,
  swiperSelect: true,
  swiperDetail: false,
  swiperFavorite: false,
  swiperFilter: false,
  prevState: [],
  clothesData: [],
  prevClothes: [],
  curClothes: [],
  layer: 2,
  fade: "",
  favoriteClothes: [],
  clothesDetail: [],
  showTop: false,
  detailClothes: null,
  detailClothesImage: null,
  detailClothesImageNumber: 0,
  filterGenderBoys: true,
  filterGenderGirls: true,
  filterGenderNeutral: true,
  filterStyleTops: true,
  filterStyleBottoms: true,
  filterStyleDresses: true,
  filterStyleOnePiece: true,
  filterStylePajamas: true,


  onSwiperClick() {
    this.swiper = !this.swiper;
    this.showTop = false;
    console.log(this.swiper);
  },

  fetchClothes() {
    const clothes = sampleList;
    this.setClothesData(clothes);

  },

  setClothesData(clothes) {
    this.clothesData = this.clothesData.concat(clothes);
  },

  setCurClothes() {
    this.curClothes.push(this.clothesData.shift());
  },

  setFavoriteClothes(favoriteClothes) {
    this.favoriteClothes = favoriteClothes;
  },

  onSelectClick(action) {
    if (action === "dislike") {
      this.fade = "swiper-fade-left";
    } else {
      //   axios.post(productsAPI, { way: "add", id: this.state.curSample[0].id });
      this.fade = "swiper-fade-right";
      this.favoriteClothes.push(this.curClothes[0]);
    }
    if (this.layer === 2) {
      this.layer = 1;
    } else {
      this.layer = 2;
    }
    this.showTop = true;
    this.prevClothes = this.curClothes;
    this.curClothes = [this.clothesData.shift()];
  },

  onDetailClick(clothes) {
    this.detailClothes = clothes;
    this.detailClothesImage = clothes.image[0];
    this.swiperDetail = true;
    if (this.swiperSelect) {
      this.prevState.push("select");
      this.swiperSelect = false;
    } else {
      this.prevState.push("favorite");
      this.swiperFavorite = false;
    }
  },

  onLeaveClick() {
    const prevPage = this.prevState.pop();
    if (this.swiperFilter === true) {
      const clothes = sampleList;
          this.curClothes = [clothes.shift()];
          this.clothesData = clothes;
          if (prevPage === "select") {
            this.swiperDetail = false;
            this.swiperFavorite = false;
            this.swiperFilter = false;
            this.swiperSelect = true;
            this.showTop = false;
          } else if (prevPage === "favorite") {
            this.swiperDetail = false;
            this.swiperSelect = false;
            this.swiperFilter = false;
            this.swiperFavorite = true;
          } else {
            this.swiperSelect = false;
            this.swiperFavorite = false;
            this.swiperFilter = false;
            this.swiperDetail = true;
          }
    } else {
      if (prevPage === "select") {
        this.swiperDetail = false;
        this.swiperFavorite = false;
        this.swiperFilter = false;
        this.swiperSelect = true;
        this.showTop = false;
      } else if (prevPage === "favorite") {
        this.swiperDetail = false;
        this.swiperSelect = false;
        this.swiperFilter = false;
        this.swiperFavorite = true;
      } else {
        this.swiperSelect = false;
        this.swiperFavorite = false;
        this.swiperFilter = false;
        this.swiperDetail = true;
      }
    }
  },

  onSelectImage(image, n) {
    this.detailClothesImage = image;
    this.detailClothesImageNumber = n;
  },

  onLeftClick() {
    if (this.detailClothesImageNumber === 0) {
      this.detailClothesImageNumber = this.detailClothes.image.length - 1;
    } else {
      this.detailClothesImageNumber = this.detailClothesImageNumber - 1;
    }
    this.detailClothesImage = this.detailClothes.image[
      this.detailClothesImageNumber
    ];
  },

  onRightClick() {
    if (this.detailClothesImageNumber === this.detailClothes.image.length - 1) {
      this.detailClothesImageNumber = 0;
    } else {
      this.detailClothesImageNumber = this.detailClothesImageNumber + 1;
    }
    this.detailClothesImage = this.detailClothes.image[
      this.detailClothesImageNumber
    ];
  },

  onFavoriteClick() {
    //   axios.get()
    this.swiperSelect = false;
    this.swiperFavorite = true;
    this.prevState.push("select");
  },

  onDeleteClick(id) {
    // axios.post(productsAPI, { way: "delete", id: id });
    let newFavoriteClothes = this.favoriteClothes.map(sample => {
      if (sample.id !== id) {
        return sample;
      }
    });
    newFavoriteClothes = newFavoriteClothes.filter(s => {
      return s;
    });
    this.favoriteClothes = newFavoriteClothes;
  },

  onFilterClick() {
    if (this.swiperSelect === true) {
      this.prevState.push("select");
      this.swiperSelect = false;
    } else if (this.swiperDetail === true) {
      this.prevState.push("detail");
      this.swiperDetail = false;
    } else if (this.swiperFavorite === true) {
      this.prevState.push("favorite");
      this.swiperFavorite = false;
    }
    this.swiperFilter = true;
  },

  onFilterGenderBoysClick() {
    this.filterGenderBoys = !this.filterGenderBoys;
  },

  onFilterGenderGirlsClick() {
    this.filterGenderGirls = !this.filterGenderGirls;
  },

  onFilterGenderNeutralClick() {
    this.filterGenderNeutral = !this.filterGenderNeutral;
  },

  onFilterStyleTopsOnlyClick() {
    this.filterStyleTops = !this.filterStyleTops;
  },

  onFilterStyleBottomsOnlyClick() {
    this.filterStyleBottoms = !this.filterStyleBottoms;
  },

  onFilterStyleDressesClick() {
    this.filterStyleDresses = !this.filterStyleDresses;
  },

  onFilterStyleOnePieceClick() {
    this.filterStyleOnePiece = !this.filterStyleOnePiece;
  },

  onFilterStylePajamasClick() {
    this.filterStylePajamas = !this.filterStylePajamas;
  }
});

export default swiperStore;



