import React, {Component} from 'react';
import Popover, { ArrowContainer } from 'react-tiny-popover';
export class HoveringPop extends Component {
    state = {
        isPopoverOpen: false,
    }
    render() {
        return (
            <Popover
                isOpen={this.state.isPopoverOpen}
                position={["bottom", "left"]}
                padding={10}
                content={({ position, targetRect, popoverRect }) => (
                    <ArrowContainer // if you'd like an arrow, you can import the ArrowContainer!
                    position={position}
                    targetRect={targetRect}
                    popoverRect={popoverRect}
                    arrowColor={"white"}
                    arrowSize={10}
                    >
                    <div
                        style={{ backgroundColor: "white", padding: '20px', width: '400px' }}
                        onClick={() => {
                        this.setState({ isPopoverOpen: !this.state.isPopoverOpen });
                        }}
                    >
                        {this.props.content}
                    </div>
                    </ArrowContainer>
                )}
            >   
                <sup 
                     style={{fontSize: "18px", color: "grey"}}
                     onMouseEnter={() => this.setState({ isPopoverOpen: !this.state.isPopoverOpen })}
                     onMouseLeave={() => this.setState({ isPopoverOpen: !this.state.isPopoverOpen })}>
                    &#9432;
                </sup>
            </Popover>
        );
    }
}


