import {
    FETCH_BLOG_DETAIL,
    UPDATE_BLOG_DETAIL,
    CREATE_NEW_BLOG
} from '../actions/blog';

const initialState = {
    blogMap: {},
    carouselBlogList: [],
    blogWallList: [],
};

export const blogReducer = (state = initialState, action) => {
    console.log(action);
    switch (action.type) {
        case CREATE_NEW_BLOG:
            return {
                ...state,
                blogMap : {
                    ...state.blogMap,
                    [action.data.id] : action.data,
                }
            }
        case UPDATE_BLOG_DETAIL:
            return {
                ...state,
                blogMap : {
                    ...state.blogMap,
                    [action.data.id] : action.data,
                }
            }
        default:
            return state;
    }
}


