import React, { Component } from "react";
import { observer } from "mobx-react";
import swiperStore from "./store";
import "./swiper.css";
import {
  SwiperWrapper,
  SwiperBackgroundImg,
  GoBackBtn,
  FilterBtn,
  SwiperDetailIntro,
  SwiperSmallImage,
  SwiperDetailLargeImageLayout,
  SwiperDetailLargeImage,
  SwiperDetailSmallImageLayout,
  SwiperDetailLeftBtn,
  SwiperDetailRightBtn
} from "./style.js";
import SA_Frame from "./static/SA_Frame.png";
import SA_Go_Back_Button from "./static/SA_Go_Back_Button.png";
import SA_Go_Back_Button_Red from "./static/SA_Go_Back_Button_Red.png";
import SA_Gray_Wheel from "./static/SA_Gray_Wheel.png";
import SA_Red_Wheel from "./static/SA_Red_Wheel.png";
import SA_L_Arrow from "./static/SA_L_Arrow.png";
import SA_R_Arrow from "./static/SA_R_Arrow.png";
import {H2, H3, H4, H5} from "../../../components/globals/index";

const DetailImg = observer(props => {
  const clothes = props.clothes;
  let number = new Array();
  for (let i = 0; i < clothes.image.length; i++) {
    number[i] = i;
  }

  const content = (
    <tr>
      {number.map(n => {
        return (
          <SwiperSmallImage
            src={clothes.image[n]}
            onClick={() => swiperStore.onSelectImage(clothes.image[n], n)}
          />
        );
      })}
    </tr>
  );

  return (
    <div>
      <SwiperDetailLargeImageLayout>
        <SwiperDetailLargeImage src={swiperStore.detailClothesImage} />
      </SwiperDetailLargeImageLayout>
      <SwiperDetailSmallImageLayout>
        <table>
          <tbody>{content}</tbody>
        </table>
      </SwiperDetailSmallImageLayout>
    </div>
  );
});

class SwiperDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      leaveHover: false,
      filterHover: false
    };
    this.mouseEnterLeaveBtn = this.mouseEnterLeaveBtn.bind(this);
    this.mouseEnterFilterBtn = this.mouseEnterFilterBtn.bind(this);
    this.mouseLeave = this.mouseLeave.bind(this);
  }

  loadingStop = () => {
    this.setState({
      loading: false
    });
  };

  mouseEnterLeaveBtn() {
    this.setState({
      leaveHover: true
    });
  }

  mouseEnterFilterBtn() {
    this.setState({
      filterHover: true
    });
  }

  mouseLeave() {
    this.setState({
      leaveHover: false,
      filterHover: false
    });
  }

  render() {
    return (
      <SwiperWrapper>
        <SwiperBackgroundImg src={SA_Frame} />
        <GoBackBtn
          onMouseOver={this.mouseEnterLeaveBtn}
          onMouseLeave={this.mouseLeave}
          src={
            this.state.leaveHover ? SA_Go_Back_Button_Red : SA_Go_Back_Button
          }
          onClick={() => swiperStore.onLeaveClick()}
        />
        <FilterBtn
          onMouseOver={this.mouseEnterFilterBtn}
          onMouseLeave={this.mouseLeave}
          src={this.state.filterHover ? SA_Red_Wheel : SA_Gray_Wheel}
          onClick={() => swiperStore.onFilterClick()}
        />
        <DetailImg clothes={swiperStore.detailClothes} />
        <SwiperDetailLeftBtn
          src={SA_L_Arrow}
          onClick={() => swiperStore.onLeftClick()}
        />
        <SwiperDetailRightBtn
          src={SA_R_Arrow}
          onClick={() => swiperStore.onRightClick()}
        />
        <SwiperDetailIntro>
          <H5 strong = {true}>{swiperStore.detailClothes.intro}
          <br />
          {swiperStore.detailClothes.madeof}
          <br />
          MSRP: ${swiperStore.detailClothes.price}</H5>
        </SwiperDetailIntro>
      </SwiperWrapper>
    );
  }
}

export default observer(SwiperDetail);



