export const CREATE_NEW_BLOG = 'CREATE_NEW_BLOG';
export const FETCH_BLOG_DETAIL = 'FETCH_BLOG_DATA';
export const UPDATE_BLOG_DETAIL = 'UPDATE_BOLG_DATA';

export const createNewBlog = (data) => {
    return {
        type: CREATE_NEW_BLOG,
        data: data
    }
}

export const fectchBlogDetail = (id) => {
    return {
        type: FETCH_BLOG_DETAIL,
        data: id
    }
}

export const updateBlogDetail = (data) => {
    return {
        type: UPDATE_BLOG_DETAIL,
        data: data
    }
}



