import styled, { css } from 'styled-components';
import Link from '../link';
import React from 'react';

/*
  default settings
 */

export const RemoveMarginPadding = css`
  margin: 0;
  padding: 0;
`

export const EWFF = {
  subtitle: "'Handlee', cursive",
  text : "'Lato', sans-serif",
  basic: "'Lato', sans-serif",
}

export const EWFS = {
  largeHeader: '64px',
  mainHeader: '48px',
  secondaryHeader: '32px',
  subheader: '24px',
  smallHeader: '16px',
  xxl: '48px',
  xl: '32px',
  lg: '24px',
  md: '16px',
  sm: '8px',
}

export const Spacing = {
  xxxl: '72px',
  xxl: '48px',
  xl: '32px',
  lg: '24px',
  md: '16px',
  sm: '8px',
  xs: '4px',
  xxs: '0px',
}


// line height
export const EWLH = {
  largeHeader: '76px',
  mainHeader: '57px',
  secondaryHeader: '38px',
  subheader: '27px',
  smallHeader: '19px',
  xxl: '57px',
  xl: '38px',
  lg: '27px',
  md: '19px',
  sm: '10px',
}

export const EWColors = new function() {
  this.dorado = '#665350';
  this.masala = '#474441';
  this.aquaIsland = '#96d6d7';
  this.apricot = '#e97f74';
  this.burntSienna = '#e75a50';
  this.akaroa = '#d7c2a4';
  this.satinLinen = '#eae7dc';
  this.naturalGray = '#8e8d8a';
  this.background = "#fcfcf5";
  this.border = '#e7e7e7';
  this.blue = '#133cbb';
  this.lightgreen = '#eae7dc';
  this.gray = '#d8d8d8';
  
}();

export const EWTextWithBackground = css`
  background-color: ${EWColors.burntSienna};
  color: white;
  font-size: 1.25rem;
  font-family: ${EWFF.text};
`

export const EWText = css`
  font-size: 1.25rem;
  font-family: ${EWFF.text};
`

export const EWCickableText = css`
  ${EWText};
  color: #777;
  line-height: 24px;
  position: relative;
  display: block;
  padding: 10px 15px
  cursor: pointer;
  text-decoration: none;
  font-size: 17px;
  font-weight: 100;
  font-family: inherit;
  transition: ease all .4s;
  &:link, &:visited {
    text-decoration: none;
  }
  &:hover {
    color: ${EWColors.burntSienna};
  }
`

export const FlexCol = styled.div`
  display: flex;
  flex-direction: column;
  align-items: stretch;
`;

export const FlexRow = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: top;
`;

// export const FontFamily = new function(){
//   this.text = "'Lato', sans-serif";
// }();


export const H1 = styled.h1`
  ${RemoveMarginPadding};
  font-family: ${EWFF.basic};
  font-size: ${EWFS.largeHeader};
  font-weight: ${props => props.strong ? '600' : '100'};
  margin: ${props => Spacing[props.spacing] || Spacing['sm']};
  line-height: ${props => EWLH[props.lineHeight] || EWLH[props.type]};
`;

export const H2 = styled.h2`
  ${RemoveMarginPadding};
  font-family: ${EWFF.basic};
  font-size: ${EWFS.mainHeader};
  font-weight: ${props => props.strong ? '600' : '100'};
  margin: ${props => Spacing[props.spacing] || Spacing['sm']};
  line-height: ${props => EWLH[props.lineHeight] || EWLH[props.type]};
`;

export const H3 = styled.h3`
  ${RemoveMarginPadding};
  font-family: ${EWFF.basic};
  font-size: ${EWFS.secondaryHeader};
  font-weight: ${props => props.strong ? '600' : '100'};
  margin: ${props => Spacing[props.spacing] || Spacing['sm']};
  line-height: ${props => EWLH[props.lineHeight] || EWLH[props.type]};
`;

export const H4 = styled.h4`
  ${RemoveMarginPadding};
  font-family: ${EWFF.basic};
  font-size: ${EWFS.subheader};
  font-weight: ${props => props.strong ? '600' : '100'};
  margin: ${props => Spacing[props.spacing] || Spacing['sm']};
  line-height: ${props => EWLH[props.lineHeight] || EWLH[props.type]};
`;

export const H5 = styled.h5`
  ${RemoveMarginPadding};
  font-family: ${EWFF.basic};
  font-size: ${EWFS.smallHeader};
  font-weight: ${props => props.strong ? '600' : '100'};
  margin: ${props => Spacing[props.spacing] || Spacing['sm']};
  line-height: ${props => EWLH[props.lineHeight] || EWLH[props.type]};
`;

export const Header = (props) => {
  switch (props.type) {
    case 'largeHeader':
      return <H1 {...props} />
    case 'mainHeader':
      return <H2 {...props} />
    case 'secondaryHeader':
      return <H3 {...props} />
    case 'subheader':
      return <H4 {...props} />
    case 'smallHeader':
      return <H5 {...props} />
    default:
      return <H5 {...props} type={"smallHeader"}/>
  }
}

export const Label = styled.label`
  ${EWText};
`

export const Img = styled.img`
  width: 100%;
`;

export const Input = styled.input`
  font-family: ${EWFF.text};
  font-size: 1rem;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
`

export const Select = styled.select`
  appearance: none;
  -webkit-appearance: none;
  border: none;
  background-color: transparent;
`;

export const BaseLinkStyle = css`
  color: #777;
  line-height: 24px;
  position: relative;
  display: block;
  padding: 10px 15px;
  cursor: pointer;
  text-decoration: none;
  font-size: 17px;
  font-weight: 100;
  font-family: inherit;
  transition: ease all .4s;
  &:link, &:visited {
    text-decoration: none;
  }
  &:hover {
    color: ${EWColors.burntSienna};
  }
`;

export const NavTab = styled(Link)`
  ${BaseLinkStyle}
`;

export const NavListWrapper = styled.ul`
  list-style: none;
  > li {
    ${props => props.floated && css`
        width: auto;
        float: left;
      `}
  }
`;



export const P = styled.p`
  ${RemoveMarginPadding};
  font-family: ${EWFF.basic};
  font-size: ${props => {
      return EWFS[props.size] || EWFS['md'];
    }
  }};
  font-weight: ${props => props.strong? '600' : '100'};
  margin: ${props => Spacing[props.spacing] || Spacing['xs']};
  line-height: ${props => EWLH[props.lineHeight] || EWLH[props.size] || EWLH['md']};
`;

export const DisableSelectInBrowser = css`
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  user-select: none;
`

export const SocialListWrapper = styled.div`
  width: 250px;
  padding-left: 10px;
  margin: 5px 0;
`;

export const Span = styled.span`
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.4;
  margin: 0;
  padding: 0;
  font-family: ${EWFF.text};
`;

export const TextArea = styled.textarea`
  ${EWText};
  flex: 1 0 auto;
  width: 100%;
  font-weight: 400;
  border: 1px solid ${EWColors.naturalGray};
  padding: 0.75rem;
  margin-top: 0.125rem;
  box-shadow: none;
  background: rgba(0,0,0,0);


  &:focus{
    border: none;
    outline: none;
    border: 1.2px solid ${EWColors.naturalGray};
  }
`;

export const LineBreaker = styled.div`
  background: ${props => (props.color || EWColors.naturalGray)};
  height: ${props => (props.height || '1px')};
  width: ${props => (props.width || '100%')};
`;

export const AspectRatioContainer = styled.div`
  width: 100%;
  padding-top: ${props => props.height};
  position: relative;
`;


