import React, {Component, ReactDOM} from 'react';
import styled from 'styled-components';
import {
  CartWrapper,
  ChildName,
  ChildWrapper,
  ProductWrapper,
  ProductInfo,
  Remove,
  ProductImg,
  CloseSign,
  Hr,
  PriceCal
} from './style';
import {RoundButton} from '../../components/newButton/RoundButton'
const ChildProduct = (props) =>{
  const {products, childName} = props.child;
  return(
    <ChildWrapper>
      <ChildName>{childName}</ChildName>
      <Hr />
      {
        products.map((product,k)=>{
          return(
            <ProductWrapper>
              <ProductImg>
              <img
                src={'https://images-na.ssl-images-amazon.com/images/I/A1i1a-1J-EL._UX679_.jpg'}
                style ={{
                  width: '100%',
                }}
              />
              </ProductImg>
              <ProductInfo>
                <div>{'Id' + product.ID}</div>
                <div></div>
                <div>{'price' + product.price}</div>
              </ProductInfo>
              <Remove >Remove <CloseSign/></Remove>

            </ProductWrapper>
          )
        })
      }
      <Hr />
    </ChildWrapper>
  )
}
class Cart extends Component {
  constructor(props){
    super(props);
    this.state ={
      closed: this.props.closed,
      children: this.props.children,
      checkoutMoney: this.props.checkoutMoney || 0,
    }
    this.toggleCart = this.toggleCart.bind(this);
  }
  toggleCart(){

    let overlay = this.state.closed;
    this.setState({closed: !overlay });
  }
  render(){
    return(
      <CartWrapper close={this.state.closed}>
        <Remove
        style={{
          margin: " 10px 0 5px 0px"
        }}
        onClick={this.toggleCart}>
          <CloseSign/> close
        </Remove>
          <div className={'ListWrapper'}>
            {
              this.state.children.map((child, k)=>{
                return(
                  <ChildProduct child={child} />
                )
              })
            }
          </div>
          <PriceCal>
            subtotal: {this.props.subtotal}
          <br />
            Total: {this.props.total}
          </PriceCal>
          <RoundButton style={{margin: 'auto'}}> confirm checkout ${this.state.checkoutMoney}</RoundButton >
      </CartWrapper>
    )
  }
};
export default Cart;



