import styled from "styled-components";
import { FlexCol, FlexRow } from "../../../../../../components/globals/index";

let ew_gray = "#d8d8d8";
let ew_button_active = "#707070";
let ew_bin_active = "#F4B7B0";

export const GenreBarsContainer = styled(FlexRow)`
	justify-content: space-around;
`;

export const GenreBar = styled(FlexCol)`
	align-items: center;
`;

export const GenreBarBinsContainer = styled(FlexCol)``;

export const GenreBarBin = styled.div`
	width: 4em;
	height: 15px;
	margin: 3px 0;
	border: 2px solid ${ew_gray};
	border-radius: 5px;
`;

export const GenreBarBinActive = styled(GenreBarBin)`
	background-color: ${ew_bin_active};
`;

export const GenreBarButtonsContainer = styled(FlexRow)`
	margin-top: 10px;
	width: 4em;
	height: 15px;
`;

export const GenreBarButton = styled.button`
	width: 50%;
	background-color: ${ew_gray};
	line-height: 0;
	outline: none;
	&:active,
	&:hover {
		background-color: ${ew_button_active};
		color: ${ew_gray};
	}
`;

export const GenreBarButtonMinus = styled(GenreBarButton)`
	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
`;

export const GenreBarButtonPlus = styled(GenreBarButton)`
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
`;



