import React, {Component} from 'react';
import { observer } from 'mobx-react';

import {
    ItemCardContainer,
    ItemCardContentWrapper,
    ItemCardDscrptContainer,
    ItemCardDscrptWrapper,
    CardText,
    CardImgFrame,
    ItemCardEventWrapper
} from './style';

import {
    P,
} from '../../../components/globals';

import {
    ImgWithPlaceholder
  } from '../../../components/img';

const ClothesImgUrl = "https://slack-imgs.com/?c=1&o1=ro&url=http%3A%2F%2Fres.cloudinary.com%2Fkaojichi%2Fimage%2Fupload%2Fv1528926424%2F1800001808%2FIMG_5419.jpg";

const ItemCard = (props) => {
    return (
        <ItemCardContainer>
            <ItemCardContentWrapper selected={props.Barcode == props.selected}
                                    onClick={() => props.onSelected(props.Barcode)}>
                <CardImgFrame>
                    <ImgWithPlaceholder
                        src={props.FrontPhoto}
                        alt="clothes"
                        placeholderHeight={"150%"}
                    />
                </CardImgFrame>
                <ItemCardDscrptContainer>
                    <ItemCardDscrptWrapper>
                        <CardText strong>{props.Description.trim() + " " + props.Genus}</CardText>
                        <CardText>{props.Brand}</CardText>
                        <CardText italic>
                            {"MSRP: " + props.MSRP + " / You Pay: " + props.EWPrice}
                        </CardText>
                    </ItemCardDscrptWrapper>
                </ItemCardDscrptContainer>
                <ItemCardEventWrapper>
                    {/* <CardText>{props.inCart? 'Add' : 'remove'}</CardText> */}
                </ItemCardEventWrapper>
            </ItemCardContentWrapper>
        </ItemCardContainer>
    );
}

ItemCard.defaultProps = {
    Size: '6Y',
    MSRP: '$80',
    EWPrice: '$55',
    FrontPhoto: "https://slack-imgs.com/?c=1&o1=ro&url=http%3A%2F%2Fres.cloudinary.com%2Fkaojichi%2Fimage%2Fupload%2Fv1528926424%2F1800001808%2FIMG_5419.jpg",
    BrandName: "LEMON BERET",
    Genus: "T-shirt",
}

export default observer(ItemCard);



