import styled from "styled-components";

export const PatternPadTop = styled.div`
    margin: 0 2.5%;
    display: inline-flex;
    align-items: center;
`
export const PatternPannelWraper = styled.div`
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
`;
export const PatternIcon = styled.div`
    width: 11%;
    padding-bottom: 4%;
    text-align: center;
    border: 1px ${props => props.status === true? "#7ddf6a": props.status ===null? "#8e8d8a": "#e75a50"} solid;
    padding-top: 4%;
    margin: 1% 2%;
    border-radius: 15px;
    
`
export const Title = styled.div`
    width: 100%;
    display: block;
    color: #e75a50;
    text-align: center;
    font-size: 22px;
`
export const FreqWrapper = styled.div`
    font-size: 14px;
    text-align: center;
    align-items: center;
    display: flex;
    margin-left: 25px;

`
export const FreqBubble = styled.div`
    margin: 2px;
    border: 1px solid gray;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    background-color: #ffffff; 
`
export const Dot = styled.div`
    width: 14px;
    align-self: auto;
    background-color: ${props => props.color};
    margin: auto;
    border-radius: 50%;
    height: 14px;
    margin-top: 2px;
`


