import {combineReducers} from 'redux';
import userReducer from './users';
import {mixingBoardReducer} from './mixingBoard';
import {blogReducer} from './blog';

const appReducer = combineReducers({
  userReducer,
  mixingBoardReducer,
  blogReducer,
});

export default appReducer;



