import styled, {css} from 'styled-components';

export const CartWrapper = styled.div`
  width: ${props => !props.close? '90vw': 0 };
  max-width: 500px;
  height: 94vh;
  position: fixed;
  display: flex;
  flex-direction: column;
  z-index: 2;
  top: 0;
  right: 0;
  background-color: white;
  border: 2px solid rgb(142, 141, 138);
  overflow-y: scroll;
  transition: 0.5s;
`
export const ChildWrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

`
export const ChildName = styled.div`
  align-self: flex-start;
  color: #ED8F85;
  font-weight: 800;
  font-size: 22px;
  margin-left: 10px;
`
export const ProductWrapper = styled.div`
  display: flex;
  flex-direction: row;
  width: 94%;
  margin: 10px 0 10px 0;
`
export const ProductInfo = styled.div`
  flex-direction: column;
  width: 60%;
  text-align: left;
  margin-left: 2%;
  color: #8e8d8a;
  font-weight: 300;
  font-style: unset;
`
export const Remove = styled.div`
  width: 25%;
  font-size: 18px;
  color: #8e8d8a;
  margin: auto;
  text-decoration: underline;
  cursor: pointer;
`
export const ProductImg = styled.div`
  width: 20%;
  border: solid 1px rgba(0,0,0,0.2);
`
export const CloseSign = styled.div`
  width: 15px;
  height: 15px;
  display: inline-block;
  position: relative;
  &:after{
    content: '';
    height: 15px;
    border-left: 2px solid #8e8d8a;
    position: absolute;
    transform: rotate(45deg);
    left: 5px;
  }
  &:before{
    content: '';
    height: 15px;
    border-left: 2px solid #8e8d8a;
    position: absolute;
    transform: rotate(-45deg);
    left: 5px;
  }
`;
export const Hr = styled.hr`
  border: 0;
  clear:both;
  display:block;
  width: 96%;
  background-color:#8e8d8a;
  height: 1px;
`
export const PriceCal = styled.div`
  margin-left: 36%;
  color: rgb(231, 90, 80);
  margin-bottom: 2em;
`



